  export default {
    name: 'MeCode',
    data (){
      return {
        isShare: false,
        list: {},
        fxImg: ''
      }
    },
    methods: {
      //分享
      shareClick(refId){
//        this.isShare = true
        const component = this.$refs[refId]
        this.http.get(this.ports.me.qrcode, res =>{
          console.log(res)
          if(res.success){
            this.fxImg = res.data
            component.show()
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      closePopup(refId){
        const component = this.$refs[refId]
        component.hide()
      },
      //分享关闭
      changeCloseShare(){
//        this.isShare = false
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      getCodeData(){
        this.http.get(this.ports.me.fxcode, res =>{
          console.log(res,9999)
          this.$store.commit('changeLoading',false)
          if(res.success){
            let data = res.data
            this.list = data
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      }
    },
    mounted (){
      this.getCodeData()
    }
  }



